* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.list {
  justify-content: center;
  gap: 100px;
  margin: 0;
  padding-top: 200px;
  display: flex;
}

.btn {
  background-color: rgba(218, 193, 193, .596);
  border: 1px solid rgba(218, 193, 193, .596);
  border-radius: 25px;
  padding: 12px 24px;
  transition: transform .25s linear, background-color .25s linear, color .25s linear;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.btn:hover, .btn:focus {
  color: #fff;
  background-color: #000;
  border: #fff;
  transform: scale(1.03);
  box-shadow: 5px 5px 7px rgba(0, 0, 0, .2), 10px 10px 10px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.video_pl {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

